import React, { useState } from "react";
import "./slideshow.scss";
import slides from "./slideshow";
import { ArrowBack, ArrowForward } from "@material-ui/icons";



const Slideshow = () => {
  const [slideIndex, setSlideIndex] = useState(0);
  const slideLength = slides.length;

  const prevSlide = () => {
    setSlideIndex(slideIndex === 0 ? slideLength - 1 : slideIndex - 1);
  };

  const nextSlide = () => {
    setSlideIndex(slideIndex === slideLength - 1 ? 0 : slideIndex + 1);
  };
  const changeSlide = () => {
    const inc = setInterval(nextSlide,1000);
  }


  return (
    <React.Fragment>
      <div className="slideshow">
        <div className="wrapper">
          <div className="left">
            <p>
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Sapiente, at laboriosam.

          
            </p>
          </div>
          <div className="right">
            <div className="slid-con">
              {slides.map((obj, index) => {
                return (
                  <React.Fragment>
                    <div
                      className={
                        slideIndex === index ? "img-con active" : "img-con"
                      }
                    >
                      <img src={obj.img} alt="" />
                    </div>
               
                  </React.Fragment>
                );
              })}
              <div className="dotcon">
              <div className="dots">
                {slides.map((obj,index)=>{
                  return(
                    <React.Fragment>
                      <span className={index === slideIndex ? "dot active":"dot"} onClick={()=>setSlideIndex(index)}></span>
                    </React.Fragment>
                  )
                })}
              </div>
              </div>

              <button onClick={prevSlide}>
                <ArrowBack />
              </button>
              <button className="next" onClick={nextSlide}>
                <ArrowForward />
              </button>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Slideshow;
