import { ExpandMore } from "@material-ui/icons";
import React, { useEffect } from "react";
import { useRef } from "react";
import "./intro.scss";
import {init} from 'ityped';
import img1 from '../../img/img2.png'
const Intro = () => {
  const textRef = useRef() ;
  useEffect(()=>{
    init(textRef.current,{
      showCursor: false,
      strings:["WebDev", "JAVA" , "Python", "C++"],
      backDelay:1500,
    });
  },[]);
  return (
    <React.Fragment>
      <div className="intro" id="intro">
        <div className="wrapper">
          <div className="intro2">
            <div className="left">
              <div className="imgcon">
                <img src={img1} alt="" />
              </div>
            </div>
            <div className="right">
              <div className="wrapper">
                <p>
                  <span className="greet">Hi There</span>
                  <br />
                  <span className="name">I'm Nitish</span><br />
                  <span className="changing-text">. </span>
                  <span ref={textRef} className="changing-text">

                  </span>
                </p>
              </div>
              <div className="expand">
                <a href="#portfolio">
                  <ExpandMore className="expand-icon"/>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Intro;