import React, { useEffect, useState } from "react";
import "./portfolio.scss";


import {
  list1,
  featuredPortfolio,
  webPortfolio,
  mobilePortfolio,
  designPortfolio,
  contentPortfolio,
} from "./data";

const Portfolio = () => {
  const [selected, setSelected] = useState("featured");
  const [data, setData] = useState([]);

  useEffect(() => {
    switch (selected) {
      case "featured":
        setData(featuredPortfolio);
        break;
      case "web":
        setData(webPortfolio);
        break;
      case "mobile":
        setData(mobilePortfolio);
        break;
      case "design":
        setData(designPortfolio);
        break;
      case "content":
        setData(contentPortfolio);
        break;
      default:
        setData(featuredPortfolio);
    }
  }, [selected]);

  
  return (
    <React.Fragment>
      <div className="portfolio" id="portfolio">
        <div className="wrapper">
          <h1>PORTFOLIO</h1>
          <div className="link-list">
            <ul>
              {list1.map((obj, index) => {
                return (
                  <React.Fragment>
                    <li className={selected === obj.id?"portfolioList active" :"portfolioList"} onClick={()=>setSelected(obj.id)}>{obj.title}</li>
                  </React.Fragment>
                );
              })}
            </ul>
          </div>
          <div className="all">
            {}
          </div>
          <div className="items">
            
            <div className="containimg">
            {data.map((obj, index)=>{
          return(
            <React.Fragment>
   
              <div className={selected===obj.id?"each-item active":"each-item"}>
                <img src={obj.img} className="fade" alt="" />

              </div>
            
            </React.Fragment>
          )
        })}
        </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Portfolio;
