import { Close, Mail, Menu } from "@material-ui/icons";
import React from "react";
import "./navbar.scss";
import navlist from "./nav";
const Navbar = ({ navActive, setNavActive }) => {
  return (
    <React.Fragment>
      <div className="navbar">
        <div className={navActive ? "wrapper active" : "wrapper"}>
          <div className="left">
            <span className="name">&lt; nitish /&gt;</span>
            <span className="mail">
              <a href="#contact">
                <i>
                  <Mail />
                </i>
                <span className="mail-text"> pqr.nitish@gmail.com</span>
               
              </a>
            </span>
          </div>
          <div className="right">
            <div className="hamburger" onClick={() => setNavActive(!navActive)}>
              {navActive ? <Close /> : <Menu />}
            </div>
          </div>
          <div className={navActive ? "menu active" : "menu"}>
            <ul>
              {navlist.map((obj) => {
                return (
                  <React.Fragment>
                    <li>
                      <a href={obj.link} onClick={()=>setNavActive(false)}>{obj.title}</a>
                    </li>
                  </React.Fragment>
                );
              })}
            </ul>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Navbar;
