import React, { useState } from "react";
import "./form.scss";

const Form = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [mobnum, setMobnum] = useState("");
  const [msg, setMsg] = useState("");
  const formSubmit = (event) => {
   

    if(name === ""){
      alert("name cant be empty")
      return 1;
    }
    else{
      if(name.length <= 3){
        alert("Please Enter a Valid Name")
        return 0;
      }
    }
    
    if(email === ""){
      alert("mail cant be empty")
   
    }
    else{
      if(email.length<=5){
        alert("enter a valid mail")
        return 0 ;
      }
    }
    
    if(mobnum === ""){
      alert("mobnum cant be empty")
      return 1;
    }
    
    if(msg === ""){
      alert("Message cant be empty")
      return 1;
    }
    else{
      if(msg.length <=4){
        alert("enter a valid message");
        return 0;
      }
    }
    uploadData(name, email, mobnum, msg);
    
  };

  const uploadData = ( name, email, mobnum, msg) => {
    alert("Thanks for contacting "+name+". I will get back to you ASAP. A computer generated confirmation mail has been sent to your entered mail");
    setMsg("")
  }
  return (
    <React.Fragment>
      <div className="form76">
        <div className="wrapper">
          <div className="inputs">
            <input type="text" placeholder="Name" onChange={(event)=>{setName(event.target.value)}} />

            <br />
            <input type="text" placeholder="Email" value={email} onChange={(event)=>{setEmail(event.target.value)}} />
            <br />
            <input type="text" placeholder="Mob. No. (optional)" value={mobnum} onChange={(event)=>{setMobnum(event.target.value)}}/>
            <br />
            <textarea
              name=""
              id=""
              cols="30"
              rows="10"
              placeholder="Leave Your message here. You will be contacted ASAP."
              value={msg} onChange={(event)=>{setMsg(event.target.value)}}
            ></textarea>
          </div>
          <div className="btn">
            <button onClick={formSubmit}>Submit</button>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Form;
